<template>
  <div
    :class="{
      modal: true,
      'modal-default-view': !isPresentationsInFullScreen,
      'modal-full-screen-view': isPresentationsInFullScreen,
    }"
  >
    <div class="modal-header">
      <span class="modal-icon close" @click="closePresentationModal()"></span>
      <span class="modal-icon"></span>
      <span
        class="modal-icon full-screen"
        @click="togglePresentationInFullScreen()"
      ></span>
    </div>
    <div
      id="presentationModalBody"
      :class="{
        'modal-body': true,
        'modal-body-default-view': !isPresentationsInFullScreen,
        'modal-body-full-screen-view': isPresentationsInFullScreen,
      }"
    >
      <div class="slide-info-strip">
        <button class="slider-btn slider-btn-left" @click="prevSlide()">
          <v-icon class="icon">mdi-chevron-left</v-icon>
        </button>
        <span class="slide-number"
          >{{ curSlide + 1 }} &nbsp;of&nbsp; {{ totalSlides }}</span
        >
        <button class="slider-btn slider-btn-right" @click="nextSlide()">
          <v-icon class="icon">mdi-chevron-right</v-icon>
        </button>
      </div>
      <carousel
        id="carousel"
        :perPage="1"
        :navigationEnabled="false"
        :paginationEnabled="false"
        :navigateTo="curSlide"
      >
        <slide class="slide"> Slide 1 </slide>
        <slide class="slide"> Slide 2 </slide>
        <slide class="slide"> Slide 3 </slide>
        <slide class="slide"> Slide 4 </slide>
        <slide class="slide"> Slide 5 </slide>
      </carousel>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    const slides = document.querySelectorAll(".slide");
    this.totalSlides = slides.length;
    this.addKeyboardEventsToToggleSlides();
  },
  unmounted() {
    window.removeEventListener("keyup");
  },
  data() {
    return {
      isPresentationsInFullScreen: false,
      curSlide: 0,
      totalSlides: undefined,
    };
  },
  methods: {
    closePresentationModal: function () {
      this.$parent.$data.isPresentationsOpen = false;
    },
    togglePresentationInFullScreen: function () {
      this.isPresentationsInFullScreen = !this.isPresentationsInFullScreen;
    },
    nextSlide: function () {
      if (this.curSlide === this.totalSlides - 1) {
        this.curSlide = 0;
      } else {
        this.curSlide++;
      }
    },
    prevSlide: function () {
      if (this.curSlide === 0) {
        this.curSlide = this.totalSlides - 1;
      } else {
        this.curSlide--;
      }
    },
    addKeyboardEventsToToggleSlides: function () {
      window.addEventListener("keyup", (e) => {
        if (e.key === "ArrowRight") {
          this.nextSlide();
        } else if (e.key === "ArrowLeft") {
          this.prevSlide();
        } else if (e.key === "Escape") {
          this.togglePresentationInFullScreen();
        }
      });
    },
  },
};
</script>

<style scoped src="../styles/Modal.scss" lang="scss"></style>
<style scoped src="../styles/PresentationModal.scss" lang="scss"></style>
